export default {
    namespaced: true,
    state: {
        departmentSubjects: []
    },
    mutations: {
        SET_DEPARTMENT_SUBJECTS: (state, data) => {
            state.departmentSubjects=data
        }
    },
    actions: {
        async fetchDepartmentSubjects({ commit }, id) {
            try {
                const response = await this.$axios.get(`/teacher/departments/${id}/subjects`)
                commit("SET_DEPARTMENT_SUBJECTS",response.data);
            }
            catch (error) {
                console.log(error.data)
            }
        }
    }
}