export default {
    namespaced: true,
    state: {
        scholarGroups: []
    },
    mutations: {
        SET_SCHOLAR_GROUPS: (state, data) => {
            state.scholarGroups=data
        }
    },
    actions: {
        async fetchScholarGroups({ commit }, id) {
            try {
                const response = await this.$axios.get(`/admin/scholar/${id}/groups`)
                commit("SET_SCHOLAR_GROUPS",response.data);
            }
            catch (error) {
                console.log(error.data)
            }
        }
    }
}