export default {
    namespaced: true,
    state: {
        departmentTeachers: []
    },
    mutations: {
        SET_DEPARTMENT_TEACHERS: (state, data) => {
            state.departmentTeachers=data
        }
    },
    actions: {
        async fetchDepartmentTeachers({ commit }, id) {
            try {
                const response = await this.$axios.get(`/teacher/departments/${id}/teachers`)
                commit("SET_DEPARTMENT_TEACHERS",response.data);
            }
            catch (error) {
                console.log(error.data)
            }
        }
    }
}