export default {
    namespaced: true,
    state: {
        departments: []
    },
    mutations: {
        SET_DEPARTMENTS: (state, data) => {state.departments = data},
        ADD_DEPARTMENT: (state, department) => {state.departments.push(department)},
        SET_DEPARTMENT_SCHOLARS: (state, data) => {
            state.departments.forEach(element => {
                if(element.id==data[0].department.id){
                    element.scholars = data 
                }
            });
        },
        SET_DEPARTMENT_SUBJECTS: (state, data) => {
            state.departments.forEach(element => {
                if(element.id==data[0].department.id){
                    element.subjects = data 
                }
            });
        },
        SET_DEPARTMENT_GROUPS: (state, data) => {
            state.departments.forEach(element => {
                if(element.id==data[0].department.id){
                    element.groups = data 
                }
            });
        },
        SET_DEPARTMENT_TEACHERS: (state, data) => {
            state.departments.forEach(element => {
                if(element.id==data[0].teacherDepartment.id){
                    element.teachers = data 
                }
            });
        }
    },
    actions: {
        //----DEPARTMENTS---//
        addDepartment({ commit }, name) {
            this.$axios.post('/admin/departments/register', { name })
            .then(function (response) {
                commit("ADD_DEPARTMENT", response.data)
            })
            .catch(function (error) {
                console.log(error+"from deps.js")
            })
            .finally(function () {
                // always executed
            });
        },
        fetchDepartments({ commit }) {
            this.$axios.get('/teacher/departments/all')
            .then(function (response) {
                commit("SET_DEPARTMENTS",response.data);
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        }
    }
}