export default {
    namespaced: true,
    state: {
        scholars: []
    },
    mutations: {
        SET_SCHOLARS: (state, data) => {state.scholars = data},
        ADD_SCHOLAR: (state, scholar) => {state.scholars.push(scholar)},
    },
    actions: {
        addScholar({ commit }, scholar) {
            this.$axios.post('/admin/scholar/register', scholar)
            .then(function (response) {
                commit("ADD_SCHOLAR", response.data)
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        },
        fetchScholars({ commit }) {
            this.$axios.get('/admin/scholar/all')
            .then(function (response) {
                commit("SET_SCHOLARS",response.data);
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        }
    }
}