export default {
    namespaced: true,
    state: {
        teachers: []
    },
    mutations: {
        SET_TEACHERS: (state, data) => {state.teachers = data},
        ADD_TEACHER: (state, teacher) => {state.teachers.push(teacher)},
    },
    actions: {
        addTeacher({ commit }, teacher) {
            this.$axios.post('/admin/teacher/register', teacher)
            .then(function (response) {
                commit("ADD_TEACHER", response.data)
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        },
        fetchTeachers({ commit }) {
            this.$axios.get('/teacher/teacher/all')
            .then(function (response) {
                commit("SET_TEACHERS",response.data);
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        }
    }
}