export default {
    namespaced: true,
    state: {
        groupScholars: []
    },
    mutations: {
        SET_GROUP_SCHOLARS: (state, data) => {state.groupScholars = data},
        ADD_SCHOLAR_GROUP: (state, data) => {state.groupScholars.push(data)},
        REMOVE_SCHOLARS_GROUP: (state, id) => {
            let len=state.groupScholars.length
            for(var i in len){
                if (id==state.groupScholars[i].id) 
                    state.groupScholars.splice(i,1)
            };
        }
    },
    actions: {
        fetchGroupScholars({ commit }, id) {
            this.$axios.get(`/admin/group/${id}/scholars`)
            .then(function (response) {
                commit("SET_GROUP_SCHOLARS",response.data);
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        },
        addScholarGroup( { commit }, ids) {
            this.$axios.post(`/admin/group/${ids.groupId}/scholar?scholarId=${ids.scholarId}`)
            .then(function (response) {
                commit("ADD_SCHOLAR_GROUP", response.data)
            })
            .catch(function () {
            })
            .finally(function () {
                // always executed
            });
        },
        async removeScholarsGroup( { commit }, ids) {
            var params = new URLSearchParams();
            ids.forEach((element,id) => {
                if(id>0) params.append("scholarId", element)
            });
            var request = {
                params: params
            }
            try {
                const response = await this.$axios.delete(`/admin/group/${ids[0]}/scholar`, request)
                    commit("REMOVE_SCHOLARS_GROUP", response.data)                
                } catch (error) {
                    console.error(error);
                }
        }
    }
}