export default {
    namespaced: true,
    state: {
        subjectGroups: []
    },
    mutations: {
        SET_SUBJECT_GROUPS: (state, data) => {state.subjectGroups = data}
    },
    actions: {
        fetchSubjectGroups({ commit }, id) {
            this.$axios.get(`/teacher/subject/${id}/groups`)
            .then(function (response) {
                commit("SET_SUBJECT_GROUPS",response.data);
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        }
    }
}