export default {
    namespaced: true,
    state: {
        scholarTests: []
    },
    mutations: {
        SET_SCHOLAR_TESTS: (state, data) => {
            state.scholarTests=data
        }
    },
    actions: {
        async fetchScholarTests({ commit }, id) {
            try {
                const response = await this.$axios.get(`/scholar/${id}/tests`)
                commit("SET_SCHOLAR_TESTS",response.data);
            }
            catch (error) {
                console.log(error.data)
            }
        }
    }
}