export default {
    namespaced: true,
    state: {
        groups: []
    },
    mutations: {
        SET_GROUPS: (state, data) => {state.groups = data},
        ADD_GROUP: (state, group) => {state.groups.push(group)},
        REMOVE_GROUP: (state, id) => {state.groups=state.groups.filter(ele=>ele.id==id)}
    },
    actions: {
        addGroup({ commit }, group) {
            this.$axios.post('/admin/group/register', group)
            .then(function (response) {
                commit("ADD_GROUP", response.data)
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        },
        fetchGroups({ commit }) {
            this.$axios.get('/admin/group/all')
            .then(function (response) {
                commit("SET_GROUPS",response.data);
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        },
        deleteGroup({ commit }, id) {
            this.$axios.delete(`/admin/group/${id}`)
            .then(function (response) {
                commit("REMOVE_GROUP", response.data)
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        },
    }
}