export default {
    namespaced: true,
    state: {
        departmentGroups: []
    },
    mutations: {
        SET_DEPARTMENT_GROUPS: (state, data) => {
            state.departmentGroups=data
        }
    },
    actions: {
        async fetchDepartmentGroups({ commit }, id) {
            try {
                const response = await this.$axios.get(`/teacher/departments/${id}/groups`)
                commit("SET_DEPARTMENT_GROUPS",response.data);
            }
            catch (error) {
                console.log(error.data)
            }
        }
    }
}