export default {
    namespaced: true,
    state: {
        tests: []
    },
    mutations: {
        SET_TESTS: (state, data) => {state.tests = data},
        ADD_TEST: (state, test) => {state.tests.push(test)},
        REMOVE_TEST: (state, id) => {state.tests=state.tests.filter(ele=>ele.id!=id)}
    },
    actions: {
        addTest({ commit }, test) {
            this.$axios.post('/teacher/test/register', test)
            .then(function (response) {
                commit("ADD_TEST", response.data)
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        },
        fetchTests({ commit }, id) {
            this.$axios.get(`/teacher/test/${id}/teacher`)
            .then(function (response) {
                commit("SET_TESTS",response.data);
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        },
        fetchAllTests({ commit }) {
            this.$axios.get(`/admin/test/all`)
            .then(function (response) {
                commit("SET_TESTS",response.data);
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        },
        deleteTest({ commit }, id) {
            this.$axios.delete(`/teacher/test/${id}`)
            .then(function (response) {
                commit("REMOVE_TEST", response.data)
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        },
    }
}