export default {
    namespaced: true,
    state: {
        testQuestions: []
    },
    mutations: {
        SET_TEST_QUESTIONS : (state, data) => {
            state.testQuestions=data
        },
        ADD_TEST_QUESTION: (state, question) => {state.testQuestions.push(question)},
        UPDATE_TEST_QUESTION: (state, question) => {state.testQuestions=state.testQuestions.map(ele=>{
            if(ele.id==question.id) ele=question
        })},
        REMOVE_TEST_QUESTION: (state, id) => {state.testQuestions=state.testQuestions.filter(ele=>ele.id!=id)},
    },
    actions: {
        async fetchTestQuestions({ commit }, id) {
            try {
                const response = await this.$axios.get(`/teacher/test/${id}/questions`)
                commit("SET_TEST_QUESTIONS",response.data);
            }
            catch (error) {
                console.log(error.data)
            }
        },
        async fetchTestQuestionsForScholar({ commit }, id) {
            try {
                const response = await this.$axios.get(`/scholar/test/${id}/questions`)
                commit("SET_TEST_QUESTIONS",response.data);
            }
            catch (error) {
                console.log(error.data)
            }
        },
        async addTestQuestion({ commit }, data) {
            try {
                const response = await this.$axios.post(`/teacher/test/${data.id}/add/question`, data.question)
                commit("ADD_TEST_QUESTION",response.data);
            }
            catch (error) {
                console.log(error.data)
            }
        },
        async deleteTestQuestion({ commit }, data) {
            try {
                const response = await this.$axios.delete(`/teacher/test/${data.id}/delete/question?questionId=${data.questionId}`)
                commit("REMOVE_TEST_QUESTION",response.data);
            }
            catch (error) {
                console.log(error.data)
            }
        },
        async updateTestQuestion({ commit }, data) {
            try {
                const response = await this.$axios.post(`/teacher/test/${data.id}/update/question`, data.question)
                commit("UPDATE_TEST_QUESTION",response.data);
            }
            catch (error) {
                console.log(error.data)
            }
        }
    }
}