export default {
    namespaced: true,
    state: {
        subjects: []
    },
    mutations: {
        SET_SUBJECTS: (state, data) => {state.subjects = data},
        ADD_SUBJECT: (state, subject) => {state.subjects.push(subject)},
        SET_SUBJECT_GROUPS: (state, data) => {state.subjects.groups = data},
    },
    actions: {
        addSubject({ commit }, subject) {
            this.$axios.post(`/admin/subject/register`, subject)
            .then(function (response) {
                commit("ADD_SUBJECT", response.data)
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        },
        fetchSubjects({ commit }) {
            this.$axios.get('/teacher/subject/all')
            .then(function (response) {
                commit("SET_SUBJECTS",response.data);
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        },
        fetchSubjectsForScholar({ commit }) {
            this.$axios.get('/scholar/subject/all')
            .then(function (response) {
                commit("SET_SUBJECTS",response.data);
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        },
        fetchSubjectGroups({ commit }, id) {
            this.$axios.get(`/teacher/subject/${id}/groups`)
            .then(function (response) {
                commit("SET_SUBJECT_GROUPS",response.data);
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        }
    }
}