export default {
    namespaced: true,
    state: {
        subjectTeachers: []
    },
    mutations: {
        SET_SUBJECT_TEACHERS: (state, data) => {state.subjectTeachers = data}
    },
    actions: {
        fetchSubjectTeachers({ commit }, id) {
            this.$axios.get(`/teacher/subject/${id}/teachers`)
            .then(function (response) {
                commit("SET_SUBJECT_TEACHERS",response.data);
            })
            .catch(function (error) {
                console.log(error.data)
            })
            .finally(function () {
                // always executed
            });
        }
    }
}