export default {
    namespaced: true,
    state: {
        teacherGroups: []
    },
    mutations: {
        SET_TEACHER_GROUPS: (state, data) => {
            state.teacherGroups=data
        }
    },
    actions: {
        async fetchTeacherGroups({ commit }, id) {
            try {
                const response = await this.$axios.get(`/teacher/${id}/groups`)
                commit("SET_TEACHER_GROUPS",response.data);
            }
            catch (error) {
                console.log(error.data)
            }
        }
    }
}